














































































































import Vue from 'vue';
import {mapMutations, mapState} from 'vuex';
import * as VuexMutation from '@/store/mutation-types';
import {Playlist} from '@/models/playlist';
import Message from '@/models/message';

const emptyPlaylistNameErrorMessage = {
  type: 'error',
  text: 'プレイリスト名が空欄です',
};

export default Vue.extend({
  name: 'PlaylistDialog',
  props: ['track'],
  data() {
    return {
      newPlaylistDialog: false,
      newPlaylistName: '',
      newPlaylistErrorMessage: null as Message | null,
      errorMessage: null as Message | null,
    };
  },
  computed: {
    ...mapState(['playlists']),
  },
  methods: {
    ...mapMutations({
      addPlaylist: VuexMutation.ADD_PLAYLIST,
      removePlaylist: VuexMutation.REMOVE_PLAYLIST,
    }),
    createPlaylist() {
      if (!this.newPlaylistName) {
        this.newPlaylistErrorMessage = emptyPlaylistNameErrorMessage;
        return;
      }
      const playlist = {
        name: this.newPlaylistName,
        tracks: [],
      };
      try {
        this.addPlaylist(playlist);
      } catch(error) {
        this.newPlaylistErrorMessage = {
          type: "error",
          text: error.message,
        };
        return;
      }
      this.newPlaylistName = '';
      this.newPlaylistDialog = false;
    },
    addTrackToPlaylist(playlist: Playlist) {
      playlist.tracks.unshift(this.track.clone());
      try {
        this.removePlaylist(playlist);
        this.addPlaylist(playlist);
      } catch(error) {
        this.errorMessage = {
          "type": "error",
          "text": error.message,
        }
        return;
      }
      this.$emit('addedTrackToPlaylist')
    },
  },
})
